const state = {
  rowIndex: null,
  cellIndex: null
}

const mutations = {
  setRow (state, rows) {
    state.rowIndex = rows
  },
  setCell (state, cell) {
    state.cellIndex = cell
  }
}

const actions = {
  addRow ({ commit }) {
    let row = state.rowIndex + 1
    commit('setRow', row)
  },
  addCell ({ commit }) {
    let cell = state.cellIndex + 1
    commit('setCell', cell)
  },
  rmRow ({ commit }) {},
  rmCell ({ commit }) {}
}

const getters = {
  getRows () {
    return state.rowIndex
  },
  getCells () {
    return state.cellIndex
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}



